import { Component, Input, OnInit } from '@angular/core';
import { CartModel } from 'src/app/model/CartModel';
import { ProductModel } from 'src/app/model/productModel';
import { CartService } from 'src/app/services/cart.service';

@Component({
  selector: 'app-cart-view',
  templateUrl: './cart-view.component.html',
  styleUrls: ['./cart-view.component.scss'],
})
export class CartViewComponent implements OnInit {

  // eslint-disable-next-line @angular-eslint/no-input-rename
  @Input('prod') productsInCart: ProductModel[]=[];

  constructor(private cartService: CartService) { }

  ngOnInit() {}

  updateQuantify(p: ProductModel, ev: any, index: number){
    const updateInCartValue=ev.target.value;
    this.cartService.updateQuantify(index, updateInCartValue);
  }

  removeItemFromCart(prod: ProductModel){
    this.productsInCart=this.cartService.removeFromCart(prod);
  }
}
